<template>
  <Home>
    <div class="background">
      <div class="safetyZone">
        <div class="leftText">
          <h3>{{PAGE_DATA.BACKGROUND_DATA.title}}</h3>
          <p class="EnTitle">{{PAGE_DATA.BACKGROUND_DATA.EnTitle}}</p>
          <p class="brief_introduction">{{PAGE_DATA.BACKGROUND_DATA.brief_introduction}}</p>
        </div>
        <el-form ref="form" :rules="rules" :model="form">
          <p class="title">{{PAGE_DATA.FORM_TITLE.title}}</p>
          <p class="subTitle">{{PAGE_DATA.FORM_TITLE.subTitle}}</p>

          <template v-for="input in PAGE_DATA.FORM_DATA">
            <el-form-item v-if="input.type == 'input'" :prop="input.EnTabel" :key="input.EnTabel">
              <el-input v-model="form[input.EnTabel]" :placeholder="input.label">
                <i slot="prefix" :class="input.icon"></i>
              </el-input>
            </el-form-item>

            <el-form-item
              v-else-if="input.type == 'select'"
              :prop="input.EnTabel"
              :key="input.EnTabel"
            >
              <el-select v-model="form[input.EnTabel]" :placeholder="input.label" clearable>
                <el-option
                  v-for="(option,key) in axiosFormData.intention"
                  :label="option"
                  :value="key"
                  :key="key"
                ></el-option>
                <i slot="prefix" :class="input.icon"></i>
              </el-select>
            </el-form-item>

            <el-form-item
              v-else-if="input.type == 'textarea'"
              :prop="input.EnTabel"
              :key="input.EnTabel"
            >
              <el-input
                v-model="form[input.EnTabel]"
                type="textarea"
                resize="none"
                :placeholder="input.label"
              ></el-input>
            </el-form-item>

            <el-form-item
              v-else-if="input.type == 'captcha'"
              :prop="input.EnTabel"
              :key="input.EnTabel"
            >
              <div class="captcha">
                <el-input v-model="form[input.EnTabel]" :placeholder="input.label"></el-input>
                <div v-loading="loading.img">
                  <img class="captchaImg" @click="getCaptcha" :src="captchaImg" />
                </div>
              </div>
            </el-form-item>
          </template>
          <el-button type="primary" @click="submit" :loading="loading.btn">立即提交</el-button>
        </el-form>
      </div>
    </div>
  </Home>
</template>

<script >
import LoginApi from "@/api/LoginApi.js";
import { PAGE_DATA } from "@/shared/LoginData.js";
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";

export default {
  name: "Login",
  data() {
    return {
      PAGE_DATA: PAGE_DATA,
      form: {
        name: "",
        company: "",
        mobile: "",
        email: "",
        intention: "",
        remark: "",
        captcha: "",
        key: ""
      }
    };
  },
  computed: {
    ...mapState("Login", {
      loading: state => state.loading,
      axiosFormData: state => state.axiosFormData,
      captcha: state => state.captcha
    }),
    ...mapGetters("Login", ["captchaImg"]),
    rules() {
      return {
        ...this.PAGE_DATA.FORM_RULES,
        mobile: [{ validator: this.validatePass, trigger: "blur" }],
        email: [{ validator: this.validatePass, trigger: "blur" }]
      };
    }
  },
  async mounted() {
    this.getFormData();
    this.getCaptcha();
  },
  methods: {
    ...mapActions("Login", ["getFormData", "getCaptcha"]),
    ...mapMutations("Login", ["setLoading"]),
    async submit() {
      try {
        await this.$refs["form"].validate();
      } catch (error) {
        return;
      }

      try {
        this.setLoading({ type: "btn", value: true });
        this.form.key = this.captcha.key;
        await LoginApi.submit(this.form);
        this.$message.success("提交成功");
        this.$refs["form"].resetFields();
      } catch (error) {
        let showErr = "";
        Object.values(error).forEach(a => {
          a.forEach(b => {
            showErr += `${b}<br/><br/>`;
          });
        });
        this.$message.error({
          dangerouslyUseHTMLString: true,
          message: showErr
        });
      }

      this.getCaptcha();
      this.setLoading({ type: "btn", value: false });
    },
    validatePass(rule, value, callback) {
      if (!this.form.mobile && !this.form.email) {
        callback(new Error(`请填写电话或邮箱`));
        return;
      }
      callback();
    }
  }
};
</script>

